import { React,useEffect,useState } from 'react'
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import { MatchSmallCard } from '../components/MatchSmallCard';
import { MatchDetailsCard } from '../components/MatchDetailsCard';
import { PieChart } from 'react-minimal-pie-chart';

import '../TeamPage.scss';

export const TeamPage = () => {
    const [team, setTeam] = useState({matches:[]});
    
    const { teamName } = useParams();

    const endYear = process.env.REACT_APP_DATA_END_YEAR;

    useEffect(
        () => {
            const fetchMatches = async () => {
                const response = await fetch(`/team/${teamName}`);

                const data = await response.json();
                console.log(data);
                setTeam(data);
                
            };

            fetchMatches();
        },[teamName]

    );


    if (!team || !team.teamName) {
        return (<h1>Team not found</h1>)
    }
    return (
        <div className="TeamPage">
            <div className="team-name-section">
                <h1 className="team-name">{team.teamName}</h1>
            </div>
            <div className="win-loss-section">Wins / Losses
            <PieChart
                    data={[
            { title: 'Losses', value: team.totalMatches-team.totalWins, color: '#e34b37' },
             { title: 'Wins', value: team.totalWins, color: '#75ab61' }
            
  ]}
/>
            
            </div>
            
            <div className="match-detail-section">
                <h3>Latest Matches</h3>
                <MatchDetailsCard teamName={team.teamName} match={team.matches[0]} />
            </div>
           
                {team.matches.slice(1).map((match) => <MatchSmallCard key={match.id} teamName={team.teamName} match={match} />)}
            

            <div className="more-link">
            <Link to={`/teams/${teamName}/matches/${endYear}`}>More </Link>
            </div>
             
            
            
        </div>
    )
}

