import { React } from 'react';
import '../TeamTile.scss';
import { Link } from 'react-router-dom';

export const TeamTile = ({ teamName }) => {
    const teamPageUrl=`/teams/${teamName}`
  return (
    <Link to={teamPageUrl}>
    <div className="TeamTile">
      <h1>{ teamName}</h1>
      
      </div>
      </Link>
  )

}
