import { React,useEffect,useState } from 'react'
import { useParams } from 'react-router';
import { MatchDetailsCard } from '../components/MatchDetailsCard';
import { YearSelector } from '../components/YearSelector';
import '../MatchPage.scss';

export const MatchPage = () => {
  const [matches, setmatcches] = useState([]);

  const { year, teamName } = useParams();
  
  useEffect(
    () => {
      const fetchMatches = async () => {
        const response = await fetch(`/team/${teamName}/matches?year=${year}`);

        const data = await response.json();
        console.log(data);
        setmatcches(data);
            
      };

      fetchMatches();
    }, [year, teamName]

  );


   
  return (
    <div className="MatchPage">
      <div className="year-selector">
       <h3> Select Year </h3>
        <YearSelector teamName={teamName} />
      </div>
      <div>
        <h1 className="page-heading">{teamName} matches in { year}</h1>
       {
        matches.map(match =>  <MatchDetailsCard key={match.id} teamName={match.teamName} match={match} />)
        }
        </div>
    </div>
  )

}
